// src/app/dashboard/page.js
"use client";

import React, { useContext } from "react";
import Link from "next/link";
import { auth } from "@/firebase/firebase";
import { signOut } from "firebase/auth";
import { useRouter } from "next/navigation";
import { toast } from "react-toastify";
import { AuthContext } from "@/context/AuthContext";

export default function Dashboard() {
  const router = useRouter();
  const { user } = useContext(AuthContext);

  const handleLogout = async () => {
    try {
      await signOut(auth);
      toast.info("Logged out successfully!");
      router.replace("/login");
    } catch (error) {
      toast.error("Error logging out: " + error.message);
    }
  };

  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-gray-50 p-4">
      <h1 className="text-4xl font-bold mb-8">Dashboard</h1>
      <div className="flex flex-col space-y-4">
        <Link 
          href="/scan"
          className="px-6 py-3 bg-blue-500 text-white rounded hover:bg-blue-600 transform hover:scale-105 transition"
        >
          Scan Product
        </Link>
        <Link 
          href="/inventory"
          className="px-6 py-3 bg-green-500 text-white rounded hover:bg-green-600 transform hover:scale-105 transition"
        >
          View Inventory
        </Link>
        <button
          onClick={handleLogout}
          className="px-6 py-3 bg-red-500 text-white rounded hover:bg-red-600 transform hover:scale-105 transition"
        >
          Logout
        </button>
      </div>
    </div>
  );
}
